import React from 'react';

const CardNoticia = ({el})=>{
    return(
<div class="card">
  <div class="card-image">
    <figure class="image is-4by3">
      <img src={el.urlToImage} alt="Placeholder image"/>
    </figure>
  </div>
  <div class="card-content" style={{overflow:"hidden"}}>
    <div class="media">
      <div class="media-content">
        <p class="title is-6">{el.author}</p>
        <p class="subtitle is-7">{el.title}</p>
      </div>
    </div>

    <div class="content">
      <p style={{fontSize:".7em"}}>{el.description}</p>
      <br/>
      <time>{el.publishedAt}</time>
    </div>
  </div>
</div>
    );
}
export default CardNoticia;