import React from 'react';

const Tags = ()=>{
    return(
        <div class="field is-grouped is-grouped-multiline"style={{position:"absolute",right:"4px",bottom:"-7px"}}>
            <div class="control">
                <div class="tags has-addons">
                    <span class="tag is-dark">
                    <img src="https://img.icons8.com/material-rounded/24/4a90e2/conference-call.png"/>
                    </span>
                    <span class="tag is-success">0</span>
                </div>
            </div>
        </div>
    );
}
export default Tags;