import React from 'react';
import ColumnStar from './ColumnStar';
import '../../css/inicio.css';
import Tags from './Tags';
const CardsSection = (props)=>{
    return(
        <div class="card" style={{border:"1px solid black"
          ,boxShadow:"1px 1px 5px"
          ,background:""}}>
          <div style={{marginLeft:"auto",marginRight:"auto",width:"3em",height:"3em",position:"absolute",top:"-1.36em"}}>
          <img src="https://img.icons8.com/fluent/48/4a90e2/paper-clamp.png"/>
          </div>
        <div class="card-content">
          <div class="media">
            <div class="media-content">
              <p class="title is-4">{props.titulo}</p>
            </div>
          </div>
          <time>{props.horasTotales}</time>
          <div class="content">
                
          </div>
            <Tags/>
            <ColumnStar/>
        </div>

      </div>  
    );

}

export default CardsSection;