import React from 'react';

const AreaTexto = ({img,titulo,mensaje,list,tam,picture})=>{
    return(
        <div class="columntext" style={{marginRight:tam,marginLeft:tam}}>
            {console.log(tam)}
            <b><img src={img}/>{titulo}</b>
            <p >
                {mensaje}
            </p>
            {picture}
            <ol style={{textAlign:"left",listStyle:"none"}}>
                {list.map((el)=>(
                    <li>
                        <a href={el.url} rel="noopener" target="_blank">
                            {el.mensaje}
                        </a>
                    </li>
                ))}
            </ol>
        </div>
    );
}
export default AreaTexto;