import React from 'react';

const SectionContent = ({titulo1,titulo2,content1,content2})=>{
    return(
        <div class="columns is-desktop" style={{marginLeft:"-1.5em"
        ,marginRight:"-1.5em",padding:"3vh"}}>
    <div class="column " style={{textAlign:"center"}}>
        <h1 style={{fontSize:"3vh",display:"block",color:"white"}}>
            {titulo1}
        </h1>
        {content1} 
    </div>
    {"\u00a0"}{"\u00a0"}
    <div class="column ">
        <h1 style={{fontSize:"3vh",color:"white",background:"#00d1b2"}}>
            {titulo2}
        </h1>
        {content2}
    </div>
</div>
    );
}
export default SectionContent;