import React from 'react';
import CardsSection from './CardsSection';
const GridCards = ({targeta})=>{
    return(
        <div class="columns">
        {targeta.map((el)=>
            (
                <div class="column">
                    <CardsSection  imagen={el.img}
                        titulo={el.title} 
                        descripcion={el.descripcion}
                        horasTotales={el.horasTotales}/>
                </div>
            )
        )}
    </div>
    );
}
export default GridCards;