import React,{useRef,useEffect,useState} from 'react';
import CardNoticia from './CardNoticia';
import Splide from '@splidejs/splide';
import {Noticias} from './ContenidoNoticias';
const Carusel = ()=>{
    const car = useRef();
    const [noticias,setNoticias] = useState([]);
    const [paginas,setPaginas] = useState(3);
    useEffect(()=>{

        console.log(window.screen.width)
        const handlenoticia = async()=>{
            try{
                const res = await fetch("https://newsapi.org/v2/everything?q=tecnologia&apiKey=079e218d0083489390dc08c25eaf6687");
                const obj = await res.json();
                if(window.screen.width<=360){
                    setPaginas(1)
                    console.log("mobil")
                }
                setNoticias( obj.articles);
              
                
            }catch(e){
                console.log(e)
            }

            
        }
        handlenoticia();
    },[])
    useEffect(()=>{
        new Splide( car.current, {
            focus:"center",
            type   : 'loop',
            perPage: paginas,
            perMove:1,
            gap:"1em",
            autoplay:true,
            interval:5000
           
          }).mount();
    });

    return(

<div className="splide" ref={car}>
	<div class="splide__track">
		<ul class="splide__list">
            {noticias.map((el)=>(
                 <li class="splide__slide"><CardNoticia el={el}/></li>
            ))}
			
		</ul>
	</div>
	<div class="splide__progress">
		<div class="splide__progress__bar">
		</div>
	</div>

</div>
    );
}
export default Carusel;